import React,{useEffect} from 'react';
import {useState} from 'react';
import './css/Modal.css';

const DetailModal = (props) => {

    const { open, close, add, header } = props;

    const[changeNum, setChangeNum] = useState(1);
    const[editTitle, setEditTitle] = useState(''); //  

    const changeTitle = (e) => {
        setEditTitle(e.target.value);
    }

    //     
    const editEvent = (e) => {
        console.log('  ');

        setChangeNum(changeNum+1); // title    (    ) title  
        props.propFunction(editTitle); // editTitle  ( title)
    }

    //     
    const deleteEvent = (e) => {
        console.log('Event ');

        props.propFunction2(deleteEvent);
    }

    useEffect(()=> {
        setEditTitle('');
    }, [changeNum])

    return (
        <div className={open ? 'openModal modal' : 'modal'}>
                  {open ? (
                    <section>
                      <header>
                        {header}
                        <button className="close" onClick={close}>
                          &times;
                        </button>
                      </header>

                    <div className="modal-body">
                        <div className="form-group">
                          <label htmlFor="taskId" className="col-form-label">  </label>
                            <input type="text" onChange={changeTitle} className="form-control" value={editTitle} id="calendar_edit_title" name="calendar_edit_title"/>
                        </div>
                    </div>

                    <footer>
                        <button className="edit" onClick={editEvent}>
                          
                        </button>
                        <button className="delete" onClick={deleteEvent}>
                          
                        </button>
                    </footer>

                    </section>
                  ) : null}
                </div>
    );
};

export default DetailModal;