import React from 'react';
import { useRef, useState, useEffect, useCallback } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import CalendarService from '../service/CalendarService';
import moment from 'moment';

import Modal from './Modal'
import DetailModal from './DetailModal'

function Calender() {

    const [events, setEvents] = useState([]);
    const [modalOpen, setModalOpen] = useState(false);

    const[calId, setCalId] = useState(0);

    const openModal = () => {
        setModalOpen(true);
    };
    const closeModal = () => {
        setModalOpen(false);
    };

    // DetailModal
    // open  Modal 
    const [detailModalOpen, setDetailModalOpen] = useState(false);

    const openDetailModal = () => {
        setDetailModalOpen(true); // detail modal open
    };

    const closeDetailModal = () => {
        setDetailModalOpen(false); // detail modal close
    };

    // Modal add    
    const addModal = (content, startDate, endDate, color) => {
        CalendarService.addEvent(content, startDate, endDate, color); // events   
        setModalOpen(false); // Modal 
    };

    // DetailModal     
    const editEvent = (editTitle) => {
        console.log(editTitle);
        console.log(' event title ');

        CalendarService.editEvent(calId, editTitle); // events  calId, title 
        setDetailModalOpen(false); // Modal 
    };

    // DetailModal     
    const deleteEvent = () => {
        console.log(calId);
        console.log(' ');

        CalendarService.deleteEvent(calId); //  event id 
        setDetailModalOpen(false); // Modal 
    };

    useEffect(()=> {
        CalendarService.getCalendar().then((res) => {
            setEvents(res.data)
        })
    }, [events]);

  return (
    <div className="Calendar">

      <Modal open={modalOpen} close={closeModal} propFunction={addModal} header=" .">
      </Modal>

      <DetailModal open={detailModalOpen} close={closeDetailModal} propFunction={editEvent} propFunction2={deleteEvent} header="Event /">
      </DetailModal>

      <FullCalendar
            plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
            initialView="dayGridMonth" //  :  
            headerToolbar={{ // , ,       
                center: 'dayGridMonth,timeGridWeek,timeGridDay new',
            }}

            displayEventTime={false}

        customButtons = {{
            new : {
                text : "new",

                click : function() {
                    openModal();
                }

            }
        }}
        events={events} // event 


        eventColor="gray"
        nowIndicator
        // dateClick={(e) => console.log(e.dateStr)}
        // eventClick={(e) => console.log(e.event.id)}

        dateClick={ (e) => openModal() }

        eventClick={ (e) => {
            // events   event
            var temp = events.find(function(data){ return (data.title==e.event.title)&&(data.start==moment(e.event.startStr).format("YYYY-MM-DD HH:mm:ss"))});

            const calId = temp.calendar_id //  event calendar_id
            setCalId(calId)

            openDetailModal()
          }
        }

      />
    </div>
  ); // return 
}

export default Calender